const Readme = ({ dataset }) => {
  let hasReadme = 'readme' in dataset;
  if(hasReadme) hasReadme = dataset.readme.file.wordCount.words > 0;
  const defaultContent = (
    <Card.Body>
      <Card.Title>Contribute!</Card.Title>
      <p>
        This dataset has no readme yet, describing how to download or use the dataset. <br/> 
        Are you familiar with this dataset? 
        Please consider <a className="text-dark">contributing</a>.
      </p>
      <Button variant="light" href={`${dataset.github_directory}/README.md`}>Edit me on GitHub!</Button>
    </Card.Body>
  );
  return (
    <div className="row mt-5">
      <Card bg="secondary" text="light" className="w-100 text-center">
        <Card.Header>Read me</Card.Header>
        {hasReadme ? <Card.Body>{dataset.readme.html}</Card.Body> : defaultContent}
        </Card>
      </div>
  );
}

function ChecksumCell({ cell }) {
  const popover = (
    <Popover>
      <Popover.Content>
        <code class='text-muted small'>{cell.value}</code>
      </Popover.Content>
    </Popover>);

  return (
    <OverlayTrigger overlay={popover} trigger="click" placement="left">
      <a class="small text-muted">{_.truncate(cell.value, {length: 5, omission: ''})}</a>
    </OverlayTrigger>
  );
}

function TruncatedCell({ cell, maxLength, omission }) {
  maxLength = maxLength || 30
  if(cell.value.length < maxLength) {
    return cell.value
  } else {
    const tooltip = <Tooltip>{cell.value}</Tooltip>
    const truncateOptions = {length: maxLength, omission: omission || '...'}
    return (
      <OverlayTrigger placement="top" overlay={tooltip}>
        <span>{_.truncate(cell.value, truncateOptions)}</span>
      </OverlayTrigger>
    );
  }
}

function RowHeaderCell({ cell }) {
  return <strong>{cell.value}</strong>
}

function OptionsCell({ cell }) {
  const row = cell.row.original

  function PillLink({ children, href, ...opts}) {
    const hasHref = href !== undefined;
    const options = _.defaults(opts, {
      as: hasHref ? 'a' : 'span',
      variant: hasHref ? 'secondary' : 'light',
      href: hasHref ? href : null,
      target: '_blank'
    })
    return <Badge pill {...options}>{children}</Badge>
  }

  return (
    <>
      <PillLink className="mr-1" href={row.preview_url} title="Preview">
        <IoIosMusicalNotes />
      </PillLink>
      <PillLink className="mr-1" href={row.url} title="Go to file">
        <IoIosLink />
      </PillLink>
      <PillLink className="mr-1" href={row.source_url} title="View code">
        <IoIosCode />
      </PillLink>
    </>
  )
}


function Index({ columns, data }) {
  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    prepareRow,
    page,
    canPreviousPage,
    canNextPage,
    pageOptions,
    pageCount,
    gotoPage,
    nextPage,
    previousPage,
    setPageSize,
    state: { pageIndex, pageSize },
  } = useTable(
    {
      columns,
      data,
      initialState: { pageIndex: 0, pageSize: 20 },
    },
    useSortBy,
    usePagination
  )

  return (
    <Card className="w-100">
      <Card.Header>Index</Card.Header>
      <Card.Body>
        <table className="table table-hover border-top-0 table-sm small table-responsive-md" 
          {...getTableProps()}>
          <thead>
              {headerGroups.map(headerGroup => (
                <tr {...headerGroup.getHeaderGroupProps()}>
                  {headerGroup.headers.map(column => (
                    // Add the sorting props to control sorting. For this example
                    // we can add them into the header props
                    <th {...column.getHeaderProps(column.getSortByToggleProps())}>
                      {column.render('Header')}
                      {/* Add a sort direction indicator */}
                      <span>
                        {column.isSorted
                          ? column.isSortedDesc
                            ? ' 🔽'
                            : ' 🔼'
                          : ''}
                      </span>
                    </th>
                  ))}
                </tr>
              ))}
            </thead>
          <tbody {...getTableBodyProps()}>
            {page.map((row, i) => {
              prepareRow(row)
              return (
                <tr {...row.getRowProps()}>
                  {row.cells.map(cell => {
                    return <td {...cell.getCellProps()}>{cell.render('Cell')}</td>
                  })}
                </tr>
              )
            })}
          </tbody>
        </table>
      </Card.Body>
      <Card.Footer className="text-center text-muted small">
          <ButtonToolbar>
            <ButtonGroup className="btn-group-sm">
              <Button variant="secondary" onClick={() => gotoPage(0)} disabled={!canPreviousPage}>
                &laquo;
              </Button>
              <Button variant="secondary" onClick={() => previousPage()} disabled={!canPreviousPage}>
                &lsaquo;
              </Button>
              <Button variant="secondary" onClick={() => nextPage()} disabled={!canNextPage}>
                &rsaquo;
              </Button>
              <Button variant="secondary" onClick={() => gotoPage(pageCount - 1)} disabled={!canNextPage}>
                &raquo;
              </Button>
            </ButtonGroup>

            <InputGroup size="sm" className="ml-2">
              <InputGroup.Prepend>
                <InputGroup.Text>Go to page</InputGroup.Text>
              </InputGroup.Prepend>
              <input type="number"
                min="1" max={pageOptions.length}
                defaultValue={pageIndex + 1}
                onChange={e => {
                  const page = e.target.value ? Number(e.target.value) - 1 : 0
                  gotoPage(page)
                }}
                style={{ width: '50px' }}
              />
              <InputGroup.Append>
                <InputGroup.Text>of {pageOptions.length}</InputGroup.Text>
              </InputGroup.Append>
            </InputGroup>

            <InputGroup size="sm" className="ml-2 mr-2">
              <InputGroup.Prepend>
                <InputGroup.Text>Songs per page</InputGroup.Text>
              </InputGroup.Prepend>
              <select className="custom-select custom-select-sm"
                value={pageSize}
                onChange={e => {setPageSize(Number(e.target.value))}}>
                {
                  [10, 20, 50, 100, 200].map(pageSize => (
                    <option key={pageSize} value={pageSize}>{pageSize}</option>
                  ))
                }
              </select>
            </InputGroup>
          </ButtonToolbar>
      </Card.Footer>
    </Card>
  );
} 

export default ({ data }) => {
  const dataset = data.metadata
  dataset.github_directory = '#'
  dataset.raw_index_url = '#'
  dataset.index = data.index

  const indexColumns = React.useMemo(
    () => [
      {
        Header: 'ID',
        accessor: 'id',
        Cell: RowHeaderCell
      },
      {
        Header: 'Options',
        accessor: row => {
          const hasPreview = row.preview_url !== undefined;
          const hasUrl = row.url !== undefined;
          const hasCode = row.source_url !== undefined;
          return `${hasPreview}${hasUrl}${hasCode}`
        },
        id: "options",
        Cell: OptionsCell
      },
      {
        Header: 'Num',
        accessor: 'source_song_num'
      },
      {
        Header: 'Title',
        accessor: 'title',
        Cell: ({cell}) => <TruncatedCell cell={cell} maxLength={30} />
      },
      {
        Header: 'Location',
        accessor: 'location',
        Cell: ({cell}) => <TruncatedCell cell={cell} maxLength={20} />
      },
      {
        Header: 'md5',
        accessor: 'checksum',
        Cell: ChecksumCell
      }
    ],
    []
  );
  const indexData = React.useMemo(() => dataset.index.songs)

  return (
    <Layout>
      <DatasetHeader dataset={dataset}/>
      <div className="row mt-5">
         <CardDeck>
            <DatasetPropsCard dataset={dataset} />
            <Technicalities dataset={dataset} />
         </CardDeck>
      </div>
      <Readme dataset={dataset} />
      <div className="row mt-5">
        { dataset.index
          ? <Index columns={indexColumns} data={indexData}/>
          : "None"
        }
      </div>
    </Layout>
  );
}

export const query = graphql`
  query ($dataset_id: String!) {
    metadata: dataset(dataset_id: {eq: $dataset_id}) {
      title
      dataset_id
      dataset_url
      tags
      description
      files
      formats
      authors {
        name
        role
        url
      }
    }
    index: file(
      relativeDirectory: {eq: $dataset_id}, 
      name: {eq: "index"}
    ) {
      songs: childrenSong {
        id
        checksum
        collection_date
        collector
        copyright
        culture
        encoding_date
        format
        genre
        has_lyrics
        has_music
        ambitus
        catalogue_numberencoder
        key
        language
        language_iso
        location
        latitude
        license_abbr
        longitude
        meter
        metric_classification
        modality
        path
        performer
        preview_url
        source
        source_author
        source_address
        source_key
        source_date
        source_page_num
        source_publisher
        source_song_num
        source_title
        title_eng
        title
        url
        version
      }
      relativePath
    }
    readme: file(relativeDirectory: {eq: $dataset_id}, name: {eq: "README"}) {
      file: childMarkdownRemark{
        html
        wordCount {
          words
        }
      }
    }
  }`